import React, { useState } from "react";
import { withRouter } from "react-router-dom";
const menuitems = [
  <div className="div_1">
    <a href="/"></a>
  </div>
 
];
const Nav = props => {
  const [open, setOpen] = useState(false);
  const { pathname } = props.location;
  const newItems = menuitems.map(item => {
    let { props } = item;
    let { className } = props;
    const addClass =
      item.props.children.props.href
        .toLowerCase()
        .replace(pathname.toLowerCase(), "") === "";
    className = `${className} ${addClass && "active"}`;
    return React.cloneElement(item, {
      ...props,
      className
    });
  });
 
  window.onscroll = () => {
    const nav = document.querySelector('.nav');
    console.log(nav.clientHeight)
    if(window.scrollY <= window.innerHeight*0.5) nav.classList.remove("nav--c")
    else nav.classList.add("nav--c")
  };
  
  return (
    
    <>
          <div className="nav" >
            <a className="vision" href="/">
              <img src={require("../../assets/img/webp/SPOT.png")} />
            </a>
            <div className="nav_2">
              
            </div>
          </div>

          {open && <main className="menu_responsive">{newItems}</main>}
    </>
  );
};
export default withRouter(props => <Nav {...props} />);
