import React, { Component } from "react";
import Form from "../Form";
import Footer from "../Footer";
import Weconncect from "../WeConect";
import Nav from "./Nav"
import { Link, Element, Events, animateScroll as scroll, } from 'react-scroll'



class Home extends Component {
  mounted = true;
  componentDidMount() {
    let particle = new window.NextParticle({
      image: this.camera,
      addTimestamp: true,
      width: Math.round(window.innerWidth * 1),
      height: window.innerHeight,
      particleGap: 2,
      particleSize: 2
    });

    const _this = this;
    window.onresize = _ => {
      if (_this.mounted) {
        particle.width = Math.round(window.innerWidth * 1);
        particle.height = window.innerHeight;
        particle.start({
          initPosition: "none",
          initDirection: "none"
        });
      }
    };
  }
  componentWillUnmount() {
    this.mounted = false;
    window.onresize = () => null;
  }
  render() {
    return (
      <div>
        <Nav />
        <div className="div-bg">
          <div className="section-1">
            <div className="div-block-1">
              <div className="particle">

                <img
                  className=""
                  ref={el => (this.camera = el)}
                  src={require("../../assets/img/webp/particle.png")}
                  alt=""
                />
              </div>

              <div className="div-block-3">

                <p className="paragraph-0 title">Webinar: La industria Retail en tiempos de <b>COVID-19</b></p>

                <p className="paragraph-1">
                  ¿Cuál es el nuevo comportamiento del consumidor?
                  <br />
                  <br />
                    Fecha: 28 de mayo <br />
                    Hora: 3:00 pm GMT-4<br />
                    Duración: 30 min<br />
                    Cupo Limitado<br />
                    GRATIS
                  </p>
                <div className="button Display-none">
                  <Link activeClass="active" className="btn-1" to="test1" spy={true} smooth={true} duration={500} >Inscribirse</Link>
                </div>
              </div>
            </div>
            <div className="div-block-2 flex items-center">
              <div className="div-block-6 ">

                <p className="paragraph-2  responseT">
                  <b>Las complicaciones que la industria Retail está enfrentando van más allá la espera a abrir
                        nuevamente las sucursales. Estamos ante un punto de inflexión del comportamiento del consumidor. </b>
                </p>

                <p className="paragraph-3 Titillium">
                  Aspectos destacados del webinar:
                  </p>
                <ul type="tipo" className="ul-1">
                  <li>Explicaremos los efectos que el COVID-19 está teniendo en la mente del consumidor de la industria retail.</li>
                  <li>Presentaremos datos reales recopilados de los nuevos patrones de consumo en los comercios físicos de los últimos 2 meses.</li>
                  <li>Introduciremos herramientas que utilizan Inteligencia Artficial y Reconocimiento Facial para el control de la afluencia y temperatura
                  de personas en las tiendas.
                    </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <Element name="test1" className="element-form">
          <Form />
        </Element>
        <div className="section-5">
          <div className="section-2">
            <div className="div-block-10">
              <div className="div-block-12">
                <p className="paragraph-4 responseT">
                  Conectamos la visión computacional a todas las industrias.
                </p>
                <p className="paragraph-5 Titillium">
                  La inteligencia artificial, combinada con la visión computacional se puede 
                  aplicar a casi cualquier industria, en SPOT buscamos implementar estas tecnologías en cualquier área comercial
                </p>
                <Weconncect />
                <button
                  onClick={() => (window.location.href = "")}
                  className="btn-3 _1 none"
                >
                  VISIT VISION WEBSITE
                </button>
              </div>
            </div>
            <div className="div-block-11">

              <video
                controls="controls"
                autoPlay
                muted
                src={require("../../assets/video/spot.webm")}
              />
            </div>
          </div>
        </div>
        <div className="div-block-25">
          <Footer />
        </div>
      </div>
    );
  }
}

export default Home;
