import React, { PureComponent } from "react";

class Weconncect extends PureComponent {
  render() {
    return (
      <div className="div-block-13">
        <div className="div-block-88 svg-2">
          <div className="div-block-89">
            <img
              src={require("../assets/img/Grupo825.svg")}
              alt="foco"
              className="foco"
            />
          </div>
          <div className="div-block-90">Retailers</div>
        </div>
        <div className="div-block-88 svg-2">
          <div className="div-block-89">
            <img
              src={require("../assets/img/aeroplane.svg")}
              alt="Airports"
              className="Airports "
            />
          </div>
          <div className="div-block-90">Aeropuertos</div>
        </div>
        <div className="div-block-88 svg-2">
          <div className="div-block-89">
            <img
              src={require("../assets/img/building.svg")}
              alt="SmartCity"
              className="building"
            />
          </div>
          <div className="div-block-90">Smart City</div>
        </div>
        <div className="div-block-88 svg-2">
          <div className="div-block-89">
            <img
              src={require("../assets/img/padlock.svg")}
              alt="Security"
              className="padlock"
            />
          </div>
          <div className="div-block-90">Seguridad</div>
        </div>
        <div className="div-block-88 svg-2">
          <div className="div-block-89">
            <img
              src={require("../assets/img/event.svg")}
              alt="Events"
              className="event"
            />
          </div>
          <div className="div-block-90">Eventos</div>
        </div>
      </div>
    );
  }
}

export default Weconncect;
