import React, { PureComponent } from "react";
import "animate.css/animate.min.css";
import { Form, Modal, Input } from 'antd';

class Forms extends PureComponent {
  state = { error: false };
  input = ({ target: { value, name } }) => this.setState({ [name]: value });
  submitData = async () => {
    if (!this.handleValidation()) return
    let body = this.state;
    body = JSON.stringify({ ...this.state, platform: "Webinar" });
    console.log(body)
    const data = await fetch("https://q18668pxed.execute-api.us-east-1.amazonaws.com/working", {
      method: "POST",
      headers: {
        "Content-type": "application/json"
      },
      body,
      //mode: 'no-cors'
    });
    const json = await data.text();
    console.log(json);
    this.setState({ done: true })
    Modal.success({
      title: "Éxito",
      content: "Datos enviados correctamente",
      onOk: () => {
        window.location.href = "http://webinar.spotcloud.io/#enviado"
      }
    })
  };


  handleValidation() {
    let fields = this.state;
    let formIsValid = true;

    //Name
    if (!fields["name"]) {
      formIsValid = false;
    }

    if (typeof fields["name"] !== "undefined") {
      if (!fields["name"].match(/^[a-züéáíóúñ]+$/gi)) {
        formIsValid = false;
      }
    }


    //Email
    if (!fields["email"]) {
      formIsValid = false;
    }

    if (typeof fields["email"] !== "undefined") {
      let lastAtPos = fields["email"].lastIndexOf("@");
      let lastDotPos = fields["email"].lastIndexOf(".");

      if (
        !(
          lastAtPos < lastDotPos &&
          lastAtPos > 0 &&
          fields["email"].indexOf("@@") == -1 &&
          lastDotPos > 2 &&
          fields["email"].length - lastDotPos > 2
        )
      ) {
        formIsValid = false;
      }
    }

    this.setState({ error: !formIsValid });
    return formIsValid;
  }



  render() {
    return (
      <div className="div-block-24">
        <div className="div-block-26">
          <div className="div-block-30">
            <p className="paragraph-12 responseT">
              Suscribete a WEBINAR, reserva tu espacio para el webinar .
             </p>
          </div>
        </div>
        <div className="div-block-27">
          <div className="div-block-28">
            <div class="">
              <Form className="div-block-29">
                <div id="form" className="topBefore">
                  <Form.Item
                    className="w-full"
                    name="name"
                    rules={[{ required: true, message: "El Nombre es necesario para el formulario" }]} /* Colocar a inputs necesarios */
                  >
                    <Input
                      id="name"
                      name="name"
                      type="text"
                      value={this.state.name}
                      onChange={this.input}
                      placeholder="Nombre completo"
                      className="mg-input"
                      requiered
                    />
                  </Form.Item>
                  <Form.Item
                    className="w-full"
                    name="email"
                    rules={[{ required: true, message: "El email es necesario para el formulario" }]} /* Colocar a inputs necesarios */
                  >
                    <Input
                      id="email"
                      name="email"
                      className=""
                      value={this.state.email}
                      onChange={this.input}
                      type="text"
                      placeholder="Email"
                      rules={[{ required: true, message: "El email es necesario para el formulario" }]}
                    />
                  </Form.Item>
                  <Input
                    id="cargo"
                    name="cargo"
                    type="text"
                    value={this.state.cargo}
                    onChange={this.input}
                    placeholder="Puesto de trabajo"
                    className="mg-input"
                    requiered
                  />
                  <Input
                    id="company"
                    name="company"
                    className=""
                    value={this.state.company}
                    onChange={this.input}
                    type="text"
                    placeholder="Empresa"
                    requiered
                  />
                  <br />
                  <div className="btndiv">
                    <input onClick={this.submitData} type="submit"
                      value="Enviar" className="btn-2 _2 btn4" />
                  </div>
                </div>
              </Form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Forms;
