import Countdown from "../Countdown"
import Nav from "./Nav"
import React, { Component } from "react";


class Send extends Component {
	mounted = true;
	componentDidMount() {
		let particle = new window.NextParticle({
			image: this.camera,
			addTimestamp: true,
			width: Math.round(window.innerWidth * 1),
			height: window.innerHeight,
			particleGap: 2,
			particleSize: 2
		});

		const _this = this;
		window.onresize = _ => {
			if (_this.mounted) {
				particle.width = Math.round(window.innerWidth * 1);
				particle.height = window.innerHeight;
				particle.start({
					initPosition: "none",
					initDirection: "none"
				});
			}
		};
	}
	componentWillUnmount() {
		this.mounted = false;
		window.onresize = () => null;
	}
	render() {
		return (
			<>
				<Nav />
				<div className="div-block-1 bg-send">
					<div className="particle">
						<img
							className=""
							ref={el => (this.camera = el)}
							src={require("../../assets/img/webp/particle.png")}
							alt=""
						/>
					</div>
					<div className="div-block-3">
						<Countdown
							timeTillDate="05 28 2019, 7:00 pm"
							timeFormat="MM DD YYYY, h:mm a"
						/>
					</div>
				</div>
			</>
		);
	}
}

export default Send;
